// 引入公共样式文件 - 使用相对路径
import './../utils/css/base.css';
import './../utils/css/commom.css';
// 引入页面css
import './login.css';
// 引入公共JS
import './../utils/js/commom.js';

console.log('login.js 加载成功!!!');

// 将验证函数移到最外层，确保全局可访问
function verifyName() {
  const userName = document.querySelector('[name=username]');
  const span = document.querySelector('.icon-moguxiaoba');
  if (!userName || !userName.value) {
    console.log('用户名输入框未找到或为空');
    return false;
  }
  const reg = /^[a-zA-Z0-9-_]{6,10}$/;
  const isValid = reg.test(userName.value);
  console.log(`用户名验证: "${userName.value}" -> ${isValid ? '通过' : '失败'}`);
  if (span) {
    if (!isValid) {
      span.classList.add('error');
    } else {
      span.classList.remove('error');
    }
  }
  return isValid;
}

function verifyPsw() {
  const psw = document.querySelector('[name=password]');
  const span = document.querySelector('.icon-suo');
  if (!psw || !psw.value) {
    console.log('密码输入框未找到或为空');
    return false;
  }
  const reg = /^[a-zA-Z0-9-_]{6,20}$/;
  const isValid = reg.test(psw.value);
  console.log(`密码验证: "${'*'.repeat(psw.value.length)}" -> ${isValid ? '通过' : '失败'}`);
  if (span) {
    if (!isValid) {
      span.classList.add('error');
    } else {
      span.classList.remove('error');
    }
  }
  return isValid;
}
document.addEventListener('DOMContentLoaded', function () {
  console.log('登录页DOM初始化开始');


  // 一、Tab栏切换业务
  const tabNav = document.querySelector('.tab-nav');
  const tabBd = document.querySelectorAll('.tab-bd');

  if (tabNav && tabBd.length > 0) {
    console.log('初始化Tab栏切换...');
    tabNav.addEventListener('click', function (e) {
      e.preventDefault();
      if (e.target.tagName === 'A') {
        console.log('Tab切换:', e.target.dataset.id);

        // 移除所有active类
        document.querySelectorAll('.tab-nav .active').forEach(item => {
          item.classList.remove('active');
        });

        // 添加active类到当前点击的标签
        e.target.classList.add('active');

        // 隐藏所有tab内容
        tabBd.forEach(tab => {
          tab.style.display = 'none';
        });

        // 显示对应的tab内容
        const tabIndex = parseInt(e.target.dataset.id);
        if (!isNaN(tabIndex) && tabBd[tabIndex]) {
          tabBd[tabIndex].style.display = 'block';
          console.log('Tab切换完成');
        }
      }
    });
    console.log('Tab栏切换初始化完成');
  } else {
    console.error('Tab栏元素未找到');
  }

  // 跳转注册页面
  const registerLink = document.querySelector('.meifeizhuce');
  if (registerLink) {
    registerLink.addEventListener('click', function (e) {
      e.preventDefault();
      console.log('跳转到注册页面');
      window.location.href = 'register.html';
    });
    console.log('注册跳转初始化完成');
  }

  // 用户名验证
  const userName = document.querySelector('[name=username]');
  if (userName) {
    userName.addEventListener('input', verifyName);
    userName.addEventListener('change', verifyName);
    console.log('用户名验证初始化完成');
  }

  // 密码验证
  const psw = document.querySelector('[name=password]');
  if (psw) {
    psw.addEventListener('input', verifyPsw);
    psw.addEventListener('change', verifyPsw);
    console.log('密码验证初始化完成');
  }

  // 勾选同意业务
  const agree = document.querySelector('.icon-queren');
  if (agree) {
    agree.addEventListener('click', function () {
      console.log('同意条款状态切换');
      this.classList.toggle('icon-queren2');
      // 添加视觉反馈
      this.style.color = this.classList.contains('icon-queren2') ? 'green' : '';
    });
    console.log('同意条款初始化完成');
  }

  // 表单提交业务
  const form = document.querySelector('form');
  if (form) {
    form.addEventListener('submit', function (e) {
      e.preventDefault();
      console.log('表单提交事件触发');

      const nameValid = verifyName();
      const pswValid = verifyPsw();
      const agreeChecked = agree ? agree.classList.contains('icon-queren2') : false;

      console.log('验证结果:', {
        用户名: nameValid ? '✅' : '❌',
        密码: pswValid ? '✅' : '❌',
        同意条款: agreeChecked ? '✅' : '❌'
      });

      if (nameValid && pswValid && agreeChecked) {
        console.log('所有验证通过！');
        localStorage.setItem('JiName', userName.value);
        alert('登录成功！');
        window.location.href = 'index.html';
      } else {
        console.log('验证失败，阻止提交');
        alert('请检查表单填写是否正确！');
      }
    });
    console.log('表单提交初始化完成');

    // 表单清空
    form.reset();
  } else {
    console.error('表单元素未找到');
  }

  console.log('login.js 所有功能初始化完成');
});